import redirect from '../../mixins/redirect.js';
import uniLoadMore from '@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
import TopDropdown from '../../components/lists/TopDropdown.vue';
import SideDrawer from '../../components/lists/SideDrawer.vue';
import GoodsLists from '../../components/lists/GoodsLists.vue';
import goods from '../../mock/goods.js';
import diyTitle from '../../components/diyTitle/diyTitle.vue';
import brandInfo from './components/brandInfo.vue';
export default {
	name: 'lists',
	mixins: [redirect],
	components: {
		GoodsLists,
		TopDropdown,
		SideDrawer,
		uniLoadMore,
		diyTitle,
		brandInfo
	},
	data() {
		return {
			imgList: [],
			titleConfig: {
				width: 0,
				height: 0,
				title: '品牌详情',
				pdt: 0
			},
			searchKey: '搜索关键词', //搜索关键词
			width: 200, //header宽度
			height: 64, //header高度
			inputTop: 0, //搜索框距离顶部距离
			arrowTop: 0, //箭头距离顶部距离
			dropScreenH: 0, //下拉筛选框距顶部距离
			attrData: [],
			attrIndex: -1,
			dropScreenShow: false,
			scrollTop: 0,
			tabIndex: 0, //顶部筛选索引
			isList: false, //是否以列表展示  | 列表或大图
			openSideDrawer: false, // 是否是打开抽屉
			sideDraweH: 0, //抽屉内部scrollview高度
			selectedName: '综合',
			orderLists: [
				{
					name: '综合',
					selected: true
				},
				{
					name: '价格升序',
					selected: false
				},
				{
					name: '价格降序',
					selected: false
				}
			],
			selectH: 0,
			goodsLists: [],
			selectIndex: -1, //选择分类
			pageIndex: 1,
			loadding: false,
			pullUpOn: true,
			loadStatus: 'more',
			searchShow: false, //搜索框
			headShow: true, //是否显示头部
			temp: null,
			navScroll: 0,
			isFixed: false,
			brandInfo: [],
			shopId: null,
			listParam: {},
			likeType: {}, //收藏/取消店铺参数
			tempSide: {},
			lock: true
		};
	},
	onLoad: function(options) {
		this.shopId = options.shopid;
		let token = uni.getStorageSync('user_token');
		this.token = token;
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		uni.getSystemInfo({
			success: res => {
				this.titleConfig.width = obj.left || res.windowWidth;
				this.titleConfig.height = obj.top ? obj.top + obj.height + 8 : res.statusBarHeight + 44;
				this.inputTop = obj.top ? obj.top + (obj.height - 30) / 2 : res.statusBarHeight + 7;
				this.titleConfig.pdt = obj.top ? obj.top + (obj.height - 32) / 2 : res.statusBarHeight + 6;

				this.height = obj.top ? obj.top + obj.height : res.statusBarHeight + 44;
				this.searchKey = options.searchKey || '';
				//略小，避免误差带来的影响
				this.dropScreenH = (this.height * 750) / res.windowWidth + this.height;
				this.sideDraweH = res.windowHeight - uni.upx2px(100) - this.height;
			}
		});
	},
	onPageScroll(e) {
		if (!this.lock) {
			return;
		}
		this.lock = false;
		setTimeout(() => {
			this.lock = true;
			if (this.navScroll - (e.scrollTop + this.titleConfig.height) <= 5) {
				this.isFixed = true;
			} else {
				this.isFixed = false;
			}
		}, 50);
	},
	mounted() {
		this.getShopInof();
		this.getGoodsList(true);
	},
	// 监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		this.loadStatus = 'loading';
		if (this.listParam.page === this.listParam.lastPage) {
			this.loadding = false;
			this.pullUpOn = false;
			this.loadStatus = 'noMore';
		} else {
			this.listParam.page++;
			setTimeout(() => {
				this.getGoodsList();
				this.loadding = false;
				this.loadStatus = 'more';
			}, 300);
		}
	},
	methods: {
		getShopInof() {
			this.$http({
				aurl: 'brandDetail',
				aPath: [this.shopId],
				success: res => {
					let result = res.data.result;
					let banner = result.shop_banner;
					if (banner !== null && banner.length > 0) {
						console.log(banner);
						banner.forEach((item, index) => {
							this.imgList.push(item.url);
						});
					}
					console.log(result);
					this.brandInfo.push(result);
					setTimeout(() => {
						const query = uni.createSelectorQuery().in(this);
						query
							.select('.em-header-screen')
							.boundingClientRect(data => {
								console.log(data);
								this.navScroll = data.top;
							})
							.exec();
					}, 100);
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		getGoodsList(init) {
			if (init) {
				this.listParam.page = 1;
				this.goodsLists = [];
				this.pullUpOn = true;
				this.listParam.shop_id = this.shopId;
			}
			this.$http({
				aurl: 'getCateList',
				data: this.listParam,
				success: res => {
					if (init) {
						this.listParam.lastPage = res.data.result.last_page;
						if (this.listParam.lastPage === 1) {
							this.loadding = false;
							this.pullUpOn = false;
							this.loadStatus = 'noMore';
						}
					}
					res.data.result.data.forEach(item => {
						this.goodsLists.push(item);
					});
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		px(num) {
			return uni.upx2px(num) + 'px';
		},
		btnSelected(e) {
			let index = e.currentTarget.dataset.index;
			this.selectIndex = index;
		},
		btnCloseDrop(ps) {
			this.scrollTop = 1;
			this.$nextTick(() => {
				this.scrollTop = 0;
			});
			this.dropScreenShow = false;
			this.attrIndex = -1;
		},
		btnSure() {
			let index = this.attrIndex;
			let arr = this.attrData;
			let active = false;
			let attrName = '';
			//这里只是为了展示选中效果,并非实际场景
			for (let item of arr) {
				if (item.selected) {
					active = true;
					attrName += attrName ? ';' + item.name : item.name;
				}
			}
			let obj = this.attrArr[index];
			this.btnCloseDrop();
			this.$set(obj, 'isActive', active);
			this.$set(obj, 'selectedName', attrName);
		},
		sure(param) {
			this.openSideDrawer = false;
			if (JSON.stringify(this.tempSide) !== JSON.stringify(param)) {
				if (JSON.stringify(this.tempSide) == '{}') {
					this.tempSide = param;
					return;
				}
				this.tempSide = param;
				this.listParam.start_price = param.ps;
				this.listParam.stop_price = param.pe;
				this.getGoodsList(true);
			}
		},
		showOrderLists() {
			this.selectH = 246;
			this.tabIndex = 0;
		},
		hideDropdownList() {
			this.selectH = 0;
		},
		selectOrderItem(e) {
			let index = Number(e.currentTarget.dataset.index);
			this.selectedName = index === 0 ? '综合' : '价格';
			this.orderLists.map((item, i) => {
				item.selected = false;
			});
			this.orderLists[index].selected = true;

			this.selectH = 0;
			this.listParam = {};
			switch (index) {
				case 0:
					//this.listParam.orderby = 'modified_time';
					break;
				case 1:
					this.listParam.orderby = 'price';
					this.listParam.direction = 'desc';
					break;
				case 2:
					this.listParam.orderby = 'price';
					this.listParam.direction = 'asc';
					break;
			}
			this.getGoodsList(true);
		},
		screen(e) {
			let index = Number(e.currentTarget.dataset.index);
			if (index < 5) {
				this.searchShow = false;
			}
			if (index !== 0) {
				this.selectH = 0; // 隐藏排序
			}

			if (index === 0) {
				if (this.selectH === 0) {
					this.showOrderLists();
				} else {
					this.hideDropdownList();
				}
			} else if (index === 1) {
				//销量
				this.tabIndex = 1;
				if (!this.listParam.orderby) {
					this.listParam = {};
				}
				this.listParam.orderby = 'sold_quantity';
				this.listParam.direction = this.listParam.direction === 'desc' ? 'asc' : 'desc';
				this.getGoodsList(true);
			} else if (index === 2) {
				this.isList = !this.isList;
			} else if (index === 3) {
				//筛选
				this.openSideDrawer = true;
			} else if (index === 4) {
				//分类
				let arr = JSON.parse(JSON.stringify(this.attrArr[1].list));
				this.attrData = arr;
				this.dropScreenShow = true;
				this.$set(this.attrArr[1], 'isActive', false);
				this.$nextTick(() => {
					this.scrollTop = 0;
				});
			} else if (index == 5) {
				//搜索
				this.searchShow = !this.searchShow;
			}
			if (this.dropScreenShow || index === 0) {
				uni.pageScrollTo({
					scrollTop: this.navScroll,
					duration: 0
				});
			}
		},
		toSearch(e) {
			this.listParam = {};
			this.listParam.keyword = e.target.value;
			this.getGoodsList(true);
		},
		closeSideDrawer(param) {
			this.openSideDrawer = false;
		},
		like(index) {
			if (!this.brandInfo[0].is_favorite) {
				this.likeType.tip = '收藏成功';
				this.likeType.url = 'ShopFavorite';
			} else {
				this.likeType.tip = '取消收藏成功';
				this.likeType.url = 'cancelShopFavorite';
			}
			this.$http({
				aurl: this.likeType.url,
				data: {
					shop_id: this.shopId
				},
				success: res => {
					this.$set(this.brandInfo[0], 'is_favorite', !this.brandInfo[0].is_favorite);
					uni.showToast({
						title: this.likeType.tip
					});
				},
				fail: err => {}
			});
		},
		toBack() {
			if (this.openSideDrawer) {
				this.closeSideDrawer();
			} else {
				uni.navigateBack();
			}
		},
		toIndex() {
			uni.switchTab({
				url: '/pages/index/index'
			});
		},
		selectItem(param) {
			console.log(111, param);
		},
		imgView(e) {
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				urls: this.imgList,
				indicator: 'number',
				current: index
			});
		}
	}
};
